.reviews {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 5px 10px;
    position: relative;
}

.redPepperWrapper {
    justify-content: center;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    position: absolute;
}

.redPepperWrapper .redPepperCon {
    display: flex;
    height: 300px;
    width: 100%;
    padding-left: 10%;
    bottom: 0px;
    position: relative;
}

.redPepperWrapper .redPepperCon img {
    align-items: center;
    justify-content: center;
    display: flex;
    height: 100%;
    width: auto;
    object-fit: contain;
}

.headerTag {
    /* background-color: aquamarine; */
    margin: auto;
    display: flex;
}

.reviewHeader {
    position: relative;
    width: 100%;
    height: auto;
    margin: auto;
}

.reviewsWrapper {
    background-color: pink;
    height: 400px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: unset;
    position: relative;
    margin: auto;
}

.commentWrapper {
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: auto;
    padding: 5px;
    width: 50%;
}

.commentWrapper .comments {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 2px solid red;
    border-radius: 10px;
    position: relative;
    height: auto;
}

.comment-textWrapper {
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: auto;
    display: flex;
    padding: 5%;
    overflow: hidden;
}

.comment-textWrapper h3 {
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: auto;
    font-family: lato;
    font-style: normal;
}

.commentWrapper .animationSlider {
    padding: 5px;
    height: auto;
    gap: 0px;
    align-items: center;
    margin: 10px;
}

.commentWrapper .comments {
    display: flex;
    backdrop-filter: blur(5px);
    background-color: transparent;
}

.commentWrapper .comments .commentImageWrapper {
    display: flex;
    height: 2px;
    width: 100%;
    position: relative;
    padding-right: 100px;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 5%;
}

.commentWrapper .comments .commentImageContainer {
    background-color: red;
    height: 100px;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    position: absolute;
    overflow: hidden;
    border: 2px solid red;
}

.commentWrapper .comments .commentImageContainer img {
    height: 100%;
    width: 100%;
    display: flex;
    margin: auto;
    object-fit: cover;
}

.commentWrapper .comments h3 {
    font-size: 15px;
    margin: auto;
    display: flex;
    font-family: lato;
    font-weight: lighter;
}

.awssld {
    height: auto;
    display: flex;
    flex-direction: column;
    gap: unset;
}

.awssld__wrapper {
    justify-content: center;
    align-items: center;
    height: 300px;
}

.awssld__content {
    background-color: transparent;
    position: relative;
    height: 300px;
    margin: 0px;
}

.awssld__controls {
    padding: 5px;
    display: flex;
    justify-content: center;
    margin: auto;
    width: 100%;
    height: auto;
}


.awssld__controls button {
    display: flex;
    opacity: .1;
    transition: 350ms all ease;
}

.awssld__controls button:hover {
    display: flex;
    opacity: 1;
}

.awssld__bullets {
    position: relative;
    bottom: initial;
    height: auto;
    top: 0px;
}

.awssld__box,
.awssld--active {
    display: flex;
}

@media only screen and (max-width: 900px) {
    .reviews {
        width: 100%;
        height: auto;
        display: flex;
        padding: 0px;
        position: relative;
        align-items: flex-end;
        flex-direction: column;
        justify-content: center;
    }

    .redPepperWrapper {
        justify-content: center;
        align-items: center;
        height: 200px;
        width: 100%;
        display: flex;
        overflow: hidden;
        position: absolute;
        bottom: 0px;
    }

    .redPepperWrapper .redPepperCon {
        overflow: hidden;
        display: flex;
        height: auto;
        width: 100%;
        bottom: 0px;
        position: relative;
        padding-left: unset;
    }

    .redPepperWrapper .redPepperCon img {
        align-items: center;
        justify-content: center;
        display: flex;
        height: auto;
        width: 100%;
        object-fit: cover;
    }

    .commentWrapper {
        justify-content: center;
        align-items: center;
        position: relative;
        display: flex;
        margin: auto;
        padding: 5px;
        height: auto;
        width: 100%;
    }

    .commentWrapper .comments {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border: 2px solid red;
        border-radius: 10px;
        position: relative;
        height: auto;
        gap: 20px;
    }
















}