.home {
    height: 100vh;
    width: 100%;
    align-items: center;
    justify-content: flex-end;
    display: flex;
    margin: auto;
}


.home .orderBtn {
    justify-content: flex-end;
    flex-direction: column;
    align-items: center;
    height: auto;
    margin: auto;
    z-index: 9;
    width: 100%;
    position: absolute;
}

.home .orderBtn .orderBtnWrapper {
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-left: 10%;
}

.home .orderBtn .orderBtnWrapper .imgLeaveContainer {

    display: flex;
    width: 100px;
    height: 400px;
    position: absolute;
    border-radius: 50%;
    overflow: hidden;
    z-index: -2;
}

.home .orderBtn .orderBtnWrapper .imgLeaveContainer img {
    position: relative;
    background-color: transparent;
    object-fit: cover;
    display: flex;
    height: inherit;
    width: auto;
}

.home .orderBtn .orderBtnWrapper .mainOrderBtn {
    gap: 10px;
    width: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.home .orderBtn .orderBtnWrapper .mainOrderBtn h3 {
    font-family: 'Just Me Again Down Here', cursive;
    font-size: 50px;
    height: auto;
    width: auto;
    flex-wrap: wrap;
    color: #E61A32;
}

.home .orderBtn .orderBtnWrapper .mainOrderBtn button {
    background-color: #E61A32;
    color: white;
    border-radius: 10px;
    border: none;
    padding: 10px 5px;
    display: flex;
    font-size: 15px;
    left: 0px;
    gap: 10px;
    display: flex;
    width: 80%;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.home .orderBtn .orderBtnWrapper .mainOrderBtn button span {
    width: auto;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.home .heroImage {
    position: relative;
    justify-content: flex-end;
    align-items: flex-end;
    position: relative;
    display: flex;
    width: 100%;
    height: 100vh;
    /* right: -30%; */
    /* top: -150px; */
    /* background-color: pink; */
}

.home .heroImage .imageHandler {
    display: flex;
    height: auto;
    width: auto;
    overflow: hidden;
    align-items: flex-end;
    justify-content: flex-end;
    transform: translate(20%, -0%);
}

.home .heroImage .mainHeroImg {
    width: auto;
    height: 100%;
    display: flex;
    overflow: hidden;
    position: relative;
    border-radius: 50%;
    align-items: flex-end;
    justify-content: center;
}

.home .heroImage .mainHeroImg img {
    position: relative;
    object-fit: cover;
    display: flex;
    height: 60rem;
    width: auto;
}

@media only screen and (max-width: 900px) {
    .home {
        height: auto;
        width: 100%;
        align-items: center;
        justify-content: center;
        display: flex;
        flex-direction: column-reverse;
        margin: auto;
    }

    .home .orderBtn {
        justify-content: center;
        flex-direction: column;
        align-items: center;
        height: auto;
        width: 100%;
        margin: auto;
        margin-bottom: 20px;
        z-index: 9;
        position: relative;
    }






    .home .heroImage {
        position: relative;
        justify-content: space-between;
        align-items: center;
        position: relative;
        display: flex;
        height: 100%;
        width: auto;

    }

    .home .heroImage .imageHandler {
        display: flex;
        height: 100%;
        width: auto;
        overflow: hidden;
        align-items: flex-start;
        justify-content: center;
        margin: auto;
        transform: translate(20%, -10%);
    }

    .home .heroImage .mainHeroImg {
        width: auto;
        height: auto;
        display: flex;
        overflow: hidden;
        position: relative;
        border-radius: 50%;
        align-items: flex-end;
        justify-content: center;

    }

    .home .heroImage .mainHeroImg img {
        position: relative;
        object-fit: cover;
        display: flex;
        height: auto;
        width: 100%;
    }
}