.about {
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.chefImageContainer {
    position: relative;
    height: 100%;
    width: auto;
    justify-content: center;
    align-items: center;
    display: flex;
}

.chefImageContainer img {
    position: relative;
    height: 100%;
    width: 100%;
    display: flex;
    object-fit: contain;
}



.aboutContentWrapper {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
}

.aboutContentWrapper .aboutWriteUp {
    padding: 20px;
    gap: 10px;
    display: flex;
    height: auto;
    width: auto;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
}

.aboutContentWrapper .aboutWriteUp h2 {
    font-family: lato;
    font-size: 35px;
    width: 100%;
    height: 100%;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    color: #5A5A5A;
}

.aboutContentWrapper .aboutWriteUp h2 span {
    margin-left: 10px;
    color: #E61A32;
    font-family: Kaushan Script;
}

.aboutContentWrapper .aboutWriteUp h3 {
    color: #5A5A5A;
    font-family: lato;
    font-weight: lighter;
    font-size: 20px;
}

.aboutContentWrapper .tomatoContainer {
    display: flex;
    height: 220px;
    width: 100%;
    overflow: hidden;
    position: relative;
    justify-content: flex-end;
    align-items: flex-end;
}

.tomatoContainer .tomatoImgContainer {
    height: auto;
    width: 60%;
    display: flex;
    object-fit: contain;
    justify-content: flex-end;
}

.tomatoContainer .tomatoImgContainer img {
    height: auto;
    width: 100%;
    display: flex;
    object-fit: cover;
}

@media only screen and (max-width: 900px) {
    .about {
        height: auto;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .chefImageContainer {
        justify-content: center;
        align-items: center;
        position: relative;
        display: flex;
        height: 400px;
        width: auto;
    }

    .aboutContentWrapper {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        position: relative;
    }

    .aboutContentWrapper .aboutWriteUp {
        padding: 30px;
        gap: 5px;
        display: flex;
        height: auto;
        width: auto;
        position: relative;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }

    .aboutContentWrapper .aboutWriteUp h2 {
        font-family: lato;
        font-size: 25px;
        width: 100%;
        height: 100%;
        display: flex;
        text-align: center;
        justify-content: center;
        align-items: center;
        color: #5A5A5A;
    }

    .aboutContentWrapper .aboutWriteUp h3 {
        color: #5A5A5A;
        font-family: lato;
        font-weight: lighter;
        text-align: center;
        font-size: 20px;
    }

    .aboutContentWrapper .tomatoContainer {
        display: flex;
        height: auto;
        width: 100%;
        overflow: hidden;
        position: relative;
        justify-content: flex-end;
        align-items: center;
    }

    .tomatoContainer .tomatoImgContainer {
        height: auto;
        width: 50%;
        display: flex;
        object-fit: contain;
        justify-content: flex-end;
    }












}