.contact {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: auto;
}

.contactHeader {
    top: 30px;
    display: flex;
    padding-left: 10%;
    width: 100%;
    position: absolute;
}

.contactContent {
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    gap: 30px;
}

.reserveForm {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
    height: auto;
    width: auto;
}

.reserveForm h3 {
    font-family: lato;
    grid-column-start: 20px;
    font-weight: lighter;
    color: lightgray;
}

.reserveForm .resevationForm {
    gap: 20px;
    width: 400px;
    margin: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.reserveForm .resevationForm input {
    width: 100%;
    border: none;
    padding: 10px;
    display: flex;
    background-color: transparent;
    border-bottom: 2px solid lightgray;
}

.reserveForm .resevationForm input::placeholder {
    color: lightgray;
}

.reserveForm .resevationForm .submitBtnWraapper {
    width: 70%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.reserveForm .resevationForm .submitBtn {
    border: none;
    right: 0px;
    width: auto;
    height: auto;
    color: red;
    padding: 10px 25px;
    border: 1px solid red;
    background-color: transparent;
    transition: all 350ms ease;
    font-weight: bold;
    cursor: pointer;
}

.reserveForm .resevationForm .submitBtn:hover {
    color: white;
}

.reservationImagesWrapper {
    justify-content: space-evenly;
    flex-direction: column;
    align-items: center;
    position: relative;
    display: flex;
    height: 500px;
    width: auto;
}

.pepperImage {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    position: absolute;
    padding-right: 5%;
    bottom: 20px;
    bottom: 0px;
}


.reservationImagesWrapper .middleImages {
    justify-content: center;
    align-items: center;
    position: relative;
    display: flex;
    height: 400px;
    width: auto;
}


.reservationImagesWrapper .middleImages .recImage {
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    margin: auto;
    height: auto;
    display: flex;
}

.restaurantImage {
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1px;
    z-index: 2;
    right: 30%;
}

.cafetaria {
    width: auto;
    height: 100%;
    display: flex;
    justify-content: center;
    overflow: hidden;
    padding: 2px;
    position: absolute;
    right: -60px;
}

.image {
    position: relative;
    height: 100%;
    width: auto;
    margin: auto;
    object-fit: cover;
}

.indexImg {
    z-index: -2;
}

.reviewsBackgroundImage {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
    z-index: -3;
    position: absolute;
}

.pepperImageContainer {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 10%;
    height: 200px;
    width: 100%;
    position: relative;
    bottom: 0px;
}

.pepperImage {
    position: relative;
    height: 100%;
    width: auto;
    object-fit: cover;
}

@media only screen and (max-width: 900px) {
    .contact {
        height: auto;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: relative;
        margin: auto;
    }

    .contactHeader {
        top: 0px;
        display: flex;
        padding-left: unset;
        width: auto;
        position: relative;
    }

    .contact .headerTag {
        width: 100%;
        justify-content: center;
        align-items: center;
        display: flex;
        margin: auto;
    }

    .contact .headerTag .contactHeader {
        width: 100%;
        height: auto;
        position: relative;
    }

    .contact .headerTag .contactHeader h1 {
        font-size: 50px;
    }

    .contactContent {
        height: auto;
        display: flex;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        width: 100%;
        gap: 5px;
    }

    .reserveForm {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        display: flex;
        height: auto;
        width: 85%;
    }

    .reserveForm .resevationForm {
        gap: 30px;
        width: 100%;
        margin: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    .reserveForm .resevationForm input {
        position: relative;
        margin: auto;
        width: 100%;
        border: none;
        padding: 10px;
        display: flex;
        background-color: transparent;
        border-bottom: 2px solid rgb(128, 128, 128);
    }

    .reserveForm .resevationForm input::placeholder {
        color: rgb(128, 128, 128);
        font-size: 15px;
    }

    .reserveForm .resevationForm .submitBtnWraapper {
        width: 100%;
        height: auto;
        display: flex;
        align-items: center;
        justify-content: center;
    }
























}